










































































import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	computed: mapState([ 'hasLogin','userInfo']),
	data() {
		return {
			title: 'navigator',
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			account:[],
			info:[],
			imagelist:[],
			tid:0,
		};
	},
	onLoad:function(e) {	
		if(this.hasLogin){
			//console.log(this.userInfo)
			this.tid = e.id;
			uni.request({
				url:`${this.$serverUrl}/index/item?id=`+e.id,
				header:{
					"Content-Type": "application/x-www-form-urlencoded",
					"token":this.userInfo.token
				},
				method:"GET",
				success: (res) => {//请求成功后返回的数据
					const data = res.data.info
					this.account = data.account;
					this.info = data.data;
					//console.log(data)
					for (let opt in this.info.other_opts) {  
						this.imagelist = this.imagelist.concat(opt);
					}
					
				}
			})
		}else{
			uni.reLaunch({
				url:"../login/login"
			})
		}
		
		
	},
	methods:{
		order(bid){
			uni.request({
				url:"http://t2.qingxinedu.cn/api/index/order?tid="+this.tid,
				header:{
					"Content-Type": "application/x-www-form-urlencoded",
					"token":this.userInfo.token
				},
				data:{bid:bid},
				method:'POST',
				success:(res)=>{
					
					if(res.data.status==1){
						
						uni.reLaunch({
							url:"../task/task",				
						})
					}else if(res.data.status==2){
						uni.showToast({
							title:'该商品已下单'
						})
					}else{
						console.log('系统错误')
					}
				},
				fail:(res)=>{
					console.log(res.data)
				},
				
			})
		},
	}
}
